import { defineDocumentType, makeSource } from 'contentlayer/source-files';

export const Host = defineDocumentType(() => ({
  name: 'Host',
  filePathPattern: `hosts/*.mdx`,
  contentType: 'mdx',
  fields: {
    dbname: {
      type: 'string',
    },
    albumtitle: {
      type: 'string',
    },
    albumartist: {
      type: 'string',
    },
    albumImageUrl: {
      type: 'string',
    },
    guest: {
      type: 'string',
    },
    episodeUrl: {
      type: 'string',
    },
  },
}));

export default makeSource({
  contentDirPath: 'data',
  documentTypes: [Host],
});
